<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col :span="8">
          <el-input placeholder="" v-model="search_id" clearable>
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="queryOrder"
            ></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="orderList" border stripe v-loading="loading">
        <!--  -->
        <el-table-column type="index" label="#"></el-table-column>
        <!--  -->
        <el-table-column label="" prop="order_number"></el-table-column>
        <!-- id -->
        <el-table-column
          label=" ID"
          prop="order_id"
          width="150px"
        ></el-table-column>
        <!--  -->
        <el-table-column
          label=""
          prop="order_price"
          width="100px"
        ></el-table-column>
        <!--  -->
        <el-table-column label="" prop="pay_status" width="100px">
          <template slot-scope="scope">
            <!--  -->
            <el-tag v-if="scope.row.pay_status === '1'" type="success"
              ></el-tag
            >
            <el-tag v-else type="danger"></el-tag>
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column label="" prop="is_send" width="90px">
        </el-table-column>
        <!--  -->
        <el-table-column label="" prop="create_time" width="180px">
          <template slot-scope="scope">
            <!--   -->
            {{ scope.row.create_time | dateFormat }}
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column label="" width="130px">
          <template slot-scope="scope">
            <!--  -->
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top"
            >
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="showBox(scope.row)"
              ></el-button>
            </el-tooltip>
            <!--  -->
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top"
            >
              <el-button
                type="success"
                icon="el-icon-location"
                size="mini"
                @click="showProgressBox"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        background
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addressVisible"
      width="50%"
      @close="addressDialogClosed"
    >
      <!--  -->
      <el-form
        :model="addressForm"
        :rules="addressFormRules"
        ref="addressFormRef"
        label-width="100px"
        class="demo-ruleForm"
      >
        <!-- / -->
        <el-form-item label="/" prop="address1">
          <el-cascader
            :options="cityData"
            v-model="addressForm.address1"
          ></el-cascader>
        </el-form-item>
        <!--  -->
        <el-form-item label="" prop="address2">
          <el-input v-model="addressForm.address2" clearable></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addressVisible = false"> </el-button>
        <el-button type="primary" @click="editAddress()"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="progressVisible" width="50%">
      <!--  -->
      <el-timeline>
        <el-timeline-item
          v-for="(activity, index) in progressInfo"
          :key="index"
          :timestamp="activity.time"
          color="#0bbd87"
        >
          {{ activity.context }}
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>
<script>
// 
import cityData from './citydata.js'
export default {
  name: 'order',
  data() {
    return {
      // 
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },
      // 
      total: 0,
      // 
      orderList: [],
      // 
      addressVisible: false,
      // 
      addressForm: {
        address1: [],
        address2: ''
      },
      // 
      addressFormRules: {
        address1: [
          { required: true, message: '/', trigger: 'blur' }
        ],
        address2: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ]
      },
      // citydata 
      cityData,
      // 
      progressVisible: false,
      // 
      progressInfo: [],
      search_id: '',
      // 
      loading: true
    }
  },
  created() {
    this.getOrderList()
  },
  methods: {
    // 
    async getOrderList() {
      const { data: res } = await this.$http.get('orders', {
        params: this.queryInfo
      })

      if (res.meta.status !== 200) {
        this.loading = false
        return this.$message.error('')
      }
      // 
      this.total = res.data.total
      this.orderList = res.data.goods
      // 
      this.loading = false
    },
    // 
    handleSizeChange(newSize) {
      this.loading = true
      this.queryInfo.pagesize = newSize
      this.getOrderList()
    },
    // 
    handleCurrentChange(newPage) {
      this.loading = true
      this.queryInfo.pagenum = newPage
      this.getOrderList()
    },
    // 
    showBox(row) {
      this.addressVisible = true
    },
    // 
    addressDialogClosed() {
      this.$refs.addressFormRef.resetFields()
    },
    // 
    editAddress() {
      this.addressVisible = false
      return this.$message.error('')
    },
    // 
    async showProgressBox() {
      // 
      const { data: res } = await this.$http.get('/kuaidi/1106975712662')

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // 
      this.progressInfo = res.data
      // 
      this.progressVisible = true
    },
    // 
    async queryOrder() {
      this.$message.error('')
    }
  }
}
</script>
<style lang="less" scoped>
.el-cascader {
  width: 100%;
}
</style>
